import CheckBox from '../../../base/checkbtn/checkbtn'
import Scroll from '../../../base/scroll/scroll'
import { mapActions, mapGetters } from 'vuex'
export default {
  name: "usersex",
  components: {
    Scroll,
    CheckBox
  },
  data() {
    return {
      chooseChecked: true,
      btnState: false
    }
  },
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
  methods: {
    setSex() {
      this.$axios.post('/api/user/update', {
        telephone: this.userInfo.telephone,
        gender: this.chooseChecked
      }).then((response) => {
        if (response.data.code === '200') {
          let _this = this
          let obj = Object.assign({}, this.userInfo, {
            gender: this.chooseChecked
          })
          this.setUserInfo(obj)
          this.$vux.toast.show({
            text: '保存成功',
            onHide() {
              _this.$router.push({ name: 'settings' });
            }
          })
        } else {
          this.$vux.toast.show({
            text: '设置失败，请重试',
            type: 'warn',
          })
        }

      }).catch((error) => {
        console.log(error)
      })
    },
    ...mapActions([
      'setUserInfo'
    ])
  },
  created() {
    if (this.userInfo.gender === 1) {
      this.chooseChecked = 1
    } else {
      this.chooseChecked = 0
    }
  }
}



